import React from 'react'
import './Ingredients.css'
import ingredientsimg from '../../assets/2.png'

const Ingredients = () => {
  return <>

      <section className='ingredient'>
        <div className='container'>
            <div className='row'>
                <div className='col-lg-6 d-flex align-items-center'>
                    <div className='content'>
                       <h3>
                          We make everything by hand with the best possible ingredients.
                       </h3>
                       <p className='my-4'>
                          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam et purus a odio finibus bibendum in sit amet leo. Mauris feugiat erat tellus.Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.
                       </p>
                       <ul>
                         <li className='d-flex align-items-center'><i className="fa-solid fa-check"></i> Etiam sed dolor ac diam volutpat.</li>
                         <li className='d-flex align-items-center'><i className="fa-solid fa-check"></i> Erat volutpat aliquet imperdiet.</li>
                         <li className='d-flex align-items-center'><i className="fa-solid fa-check"></i> purus a odio finibus bibendum.</li>
                       </ul>
                       <button>Learn More </button>
                    </div>
                </div>


                <div className='col-lg-6'>
                    <div>
                        <img  className='img-fluid' src={ingredientsimg} alt='prideimg'  />
                    </div>
                </div>
            </div>
        </div>
      </section>

  
  </>
}

export default Ingredients