import React from 'react'
import './Nav.css'
import logo from './../../assets/logo.png'

const Nav = () => {
  return (
    <>

<nav className="navbar navbar-expand-lg navbar-light position-fixed w-100" id='navbar'>
  <div className="container">
    <a className="navbar-brand" href="#"><img  src={logo} title='logo' alt='logo' /></a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link" href="#">Home</a>
        </li>

        <li className="nav-item">
          <a className="nav-link" href="#">About Us</a>
        </li>

        <li className="nav-item">
          <a className="nav-link" href="#">Explore Foods</a>
        </li>

        <li className="nav-item">
          <a className="nav-link" href="#">Reviews</a>
        </li>

        <li className="nav-item">
          <a className="nav-link" href="#">FAQ</a>
        </li>

        <li className="nav-item last_link">
          <a className="nav-link" href="#">1800 789 123</a>
        </li>
      </ul>

     
    </div>



  </div>
</nav>
       
    </>
  )
}

export default Nav