import React from 'react'
import './Food.css'
import blog1 from '../../assets/01.jpg'
import blog2 from '../../assets/02.jpg'
import blog3 from '../../assets/03.jpg'

const Food = () => {

   const products = [
    {
       imgproduct : blog1 ,
       title:"Rainbow Vegetable Sandwich",
       time:"Time: 15 - 20 Minutes | Serves: 1",
       newprice: "$10.50",
       oldprice:"$11.70"
    } , 
    {
        imgproduct : blog2 ,
        title:"Vegetarian Burger",
        time:"Time: 30 - 45 Minutes | Serves: 1",
        newprice: "$9.20",
        oldprice:"$10.50"
     } ,
     {
        imgproduct : blog3 ,
        title:"Raspberry Stuffed French Toast",
        time:"Time: 10 - 15 Minutes | Serves: 1",
        newprice: "$12.50",
        oldprice:"$13.20"
     }
 
 

]

    
  return <>

      <section className='food'>
        <div>
            <h3 className='text-center'>Explore Our Foods</h3>
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam et purus a odio finibus bibendum in sit amet leo. Mauris feugiat erat tellus. Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts. Separated they live in Bookmarksgrove.</p>
        </div>

        <div className='container'>
        <div className='row'>
        { products.map( (product , idx)=>{ return <div className='col-lg-4' key={idx}>
            
            <div className='item'>
                <img className='img-fluid' src={product.imgproduct} />
                <h5>{product.title}</h5>
                <p>{product.time}</p>
                <span>{product.newprice}</span>
                <del>{product.oldprice}</del>
            </div>
            <div className='bottom_btn'>
                <button>Order</button>
            </div>
           
         </div>
       
      }) }
        </div>
      </div>
      
      </section>

   
  
      
  
  </>
}

export default Food