import React from 'react'
import './Testimonial.css'
import Person1 from '../../assets/person1.jpg'
import Person2 from '../../assets/person2.jpg'

export const Testimonial = () => {
  return <>

   <section className='testimonial'>
     <h2 className='text-center'>Testimonials</h2>
       <div className='container'>
         <div className='row'>
            <div className='col-lg-12'>

            <div id="carouselExampleCaptions" className="carousel slide" data-bs-ride="carousel">
           
            <div className="carousel-inner">
                <div className="carousel-item active">
                <div className="">
                    <img src={Person1} className="d-block" alt="person" />
                    <p>"Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts. Separated they live."</p>
                    <h5>Simab Dave - Web Designer</h5>
                </div>
                </div>
                <div className="carousel-item">
                <div className="">
                    <img src={Person2} className="d-block" alt="person" />
                    <p>"Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts. Separated they live far from the countries Vokalia."</p>
                    <h5>Johnthan Doe - UX Designer</h5>
                </div>
                </div>
                <div className="carousel-item">
                <div className="">
                    <img src={Person1} className="d-block" alt="person" />
                    <p>"Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts. "</p>
                    <h5>Maccy Doe - Front End</h5>
                </div>
                </div>
            </div>

            <div className="carousel-indicators">
                <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="1" aria-label="Slide 2"></button>
                <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="2" aria-label="Slide 3"></button>
            </div>


            </div>


            </div>
         </div>
       </div>
   </section>
  
  </>
}

export default Testimonial